@import url(https://fonts.googleapis.com/css?family=Cedarville+Cursive|Quicksand);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

html, body {
  height: 100%; }

body {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  text-align: center;
  background-color: lightgreen;
  font-family: 'Quicksand', sans-serif;
  font-size: 16px; }

#quote-box {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  text-align: center; }
  #quote-box h1 {
    font-size: 2.5em;
    font-weight: bold;
    width: 100%; }
  #quote-box #text {
    background-color: white;
    font-family: 'Cedarville Cursive', cursive;
    font-size: 1.5em;
    margin: 20px auto;
    padding: 20px;
    width: 80%; }
  #quote-box #author {
    font-size: 2em;
    width: 100%; }
  #quote-box #buttons {
    align-items: center;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    text-align: center;
    margin-top: 5%; }
    #quote-box #buttons button {
      font-size: 2em;
      margin: 0 20px;
      padding: 20px; }
      #quote-box #buttons button a {
        text-decoration: none; }

